import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-evo',
  templateUrl: './evo.page.html',
  styleUrls: ['./evo.page.scss'],
})
export class EvoPage implements OnInit {

  score: number;
  constructor() { }

  ngOnInit() {
    this.score=0;
  }

  sumar(){
    if (this.score<10)
      this.score++;
    else{
      alert("Has Ganado");
      this.score=0;
    }
  }

  restar(){
    if (this.score>0)
      this.score--;
    else
      alert('No puntuaciones negativas')

  }

}
